import axios from "axios";

export const clientMixin = {
    data() {
        return {
            clients: [],
            selectedClient: null,
            options: {},
            messages: [],
            streamSource: null,
            isLoading: false,
            isTyping: false,
            editDialog: {
                form: {
                    clientId: null,
                    name: "",
                    description: "",
                },
                visible: false,
            },
        };
    },
    methods: {
        // 
        async fetchClients() {
            try {
                const response = await axios.get(`client/valid`);
                this.clients = response.data;
                if (this.clients.length > 0) {
                    this.selectClient(this.clients[0]);
                }
            } catch (error) {
                console.error("Error fetching clients:", error);
                this.$message.error("");
            }
        },

        // 
        async selectClient(client) {
            if (this.streamSource) {
                this.streamSource.close();
                this.streamSource = null;
            }
            this.isLoading = false;
            this.isTyping = false;

            // 
            this.messages = this.loadChatHistory(client.id);

            try {
                const response = await axios.get(`client/${client.id}`);
                this.selectedClient = response.data;
                this.options = JSON.parse(response.data.optionsJsonStr || '{}');
                await this.loadClientContext()

                // 
                await Promise.all([
                    this.loadMcpTools(),
                    this.loadAdvisors(),
                    this.loadFileList()
                ]);

                // 
                this.$nextTick(() => {
                    this.scrollToBottom();
                });
            } catch (error) {
                console.error("Error fetching client details:", error);
                this.$message.error("");
            }
        },

        // 
        async createNewClient() {
            try {
                await axios.post(`client`);
                this.$message.success("");
                await this.fetchClients();
            } catch (error) {
                console.error("Error creating client:", error);
                this.$message.error("");
            }
        },

        // 
        openEditDialog(client) {
            this.editDialog.form = {
                clientId: client.id,
                name: client.clientName,
                description: client.description || "",
            };
            this.editDialog.visible = true;
        },

        // 
        async saveClient() {
            try {
                const {clientId, name, description} = this.editDialog.form;
                await axios.put(`client`, null, {
                    params: {clientId, name, description},
                });
                this.$message.success("");
                this.editDialog.visible = false;
                await this.fetchClients();
            } catch (error) {
                console.error("Error saving client:", error);
                this.$message.error("");
            }
        },

        // 
        async deleteClient(client) {
            try {
                await this.$confirm("", "", {
                    type: "warning",
                });

                await axios.delete(`client/${client.id}`);

                // 
                localStorage.removeItem(`chat_history_${client.id}`);

                this.$message.success("");
                await this.fetchClients();
            } catch (error) {
                if (error !== "cancel") {
                    console.error("Error deleting client:", error);
                    this.$message.error("");
                }
            }
        },

        // 
        async handleClientConfigChange() {
            try {
                await axios.put(`client/config`, {
                    ...this.selectedClient,
                    clientId: this.selectedClient.id,
                    options: this.options
                });
                this.$message.success("");
            } catch (error) {
                console.error("Error updating client config:", error);
                this.$message.error("");
            }
        },

        // 
        loadChatHistory(clientId) {
            const chatHistory = localStorage.getItem(`chat_history_${clientId}`);
            return chatHistory ? JSON.parse(chatHistory) : [];
        },

        // 
        scrollToBottom() {
            const messagesContainer = this.$refs.messagesContainer;
            if (messagesContainer) {
                messagesContainer.scrollTop = messagesContainer.scrollHeight;
            }
        },


    }
};